import { Link, useLocation, useParams } from 'react-router-dom';

import { useAppSelector } from '../redux/hooks';

type paramsProps = { productId: string };

const ProductsDetails = () => {
  const { productId } = useParams<paramsProps>();
  const products = useAppSelector(state => state.products);
  const product = products.find(good => good.id === productId);

  const location = useLocation();
  const backLink = location.state?.from ?? '/products';

  return (
    <div>
      <Link to={backLink}>Back to products</Link>
      <h2>
        {product?.name} and {product?.id}
      </h2>
    </div>
  );
};

export default ProductsDetails;
